import Endpoint from '@/services/Endpoint'

export default {
  reportApps (payload) {
    return Endpoint().get('/s1/connect/report/app', {
      params: payload
    })
  },
  reportParameters (payload) {
    return Endpoint().get(`/s1/connect/report/${payload.reportId}/parameter`, {
      params: payload
    })
  },
  reportPrice (payload) {
    return Endpoint().get(`/s1/connect/report/${payload.reportId}/price`, {
      params: payload
    })
  },
  runReport (payload) {
    return Endpoint().post(`/s1/connect/report/${payload.reportId}/run`, payload)
  },
  reportWorkflowInstances (payload) {
    return Endpoint().get('/s1/connect/report/workflow/instance', {
      params: payload
    })
  }
}
